.profile-container {
  max-width: 500px;
  margin: 40px auto;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 0 10px #ccc;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.profile-container input {
  width: 500px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.avatar-preview {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  text-align: center;
}

.delete-btn {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.update-btn {
  grid-column: span 2;
  margin-top: 10px;
  padding: 9px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.update-btn:hover {
  background-color: #218838;
}

.profile-photo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 120px;
}

.field-inline {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 5px;
}

.edit-icon {
  cursor: pointer;
  color: #555;
  transition: 0.2s;
}

.edit-icon:hover {
  color: #000;
}

.field-group {
  margin-bottom: 20px;
}

@media (max-width: 480px) {
  .profile-container {
    margin: 20px 10px;
    padding: 16px;
    box-shadow: none;
  }

  .profile-container input {
    width: 100%;
    font-size: 14px;
  }

  .avatar-preview {
    width: 100px;
    height: 100px;
  }

  .update-btn {
    width: 100%;
    padding: 8px;
    font-size: 14px;
  }

  .delete-btn {
    width: 100%;
    padding: 10px;
    font-size: 15px;
  }

  .field-inline {
    flex-direction: column;
    align-items: stretch;
  }

  .field-group label {
    font-size: 14px;
  }

  .edit-icon {
    align-self: flex-end;
    margin-top: 5px;
  }
}
