h1 {
    color: #11263A;
}

.input1 {
    width: 200px;
    height: 55px;
    border: 1px solid silver;
    border-radius: 5px;
}

.input1:hover{
    border: solid 1px black;
}

.btn-search {
  width: 180px;
  height: 55px;
  background-color: #11263A;
  color: white;
  border: 1px solid #11263A;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}


.btn-search:hover {
    background-color: #0f1f2a;
}

.btn-search:active {
    background-color: #0b141d;  
    border-color: #0b141d;
}

.btn-search:focus {
    outline: none;
    background-color: #1a344a;
}

.form-control {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
  align-items: flex-end;
  border: 0px;
}

.form-control .MuiFormControl-root {
  min-width: 180px;
  max-width: 220px;
  width: 100%;
}

.MuiInputBase-root {
  height: 55px;
  box-sizing: border-box;
}

.MuiFormControl-root .MuiInputBase-root input {
  height: 24px;
}

@media (max-width: 768px) {
  .form-control {
    flex-direction: column;
    align-items: center;
  }

  .btn-search {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.5rem;
    line-height: 1.4;
  }

  .form-control {
    padding: 0 10px;
    gap: 10px;
  }

  .form-control .MuiFormControl-root {
    min-width: 100%;
    max-width: 100%;
  }

  .btn-search {
    font-size: 15px;
    height: 50px;
  }

  .MuiInputBase-root {
    height: 50px;
  }

  .MuiInputBase-input {
    font-size: 14px;
  }

  .MuiInputLabel-root {
    font-size: 14px;
  }

  .MuiFormLabel-root {
    font-size: 13px;
  }

  img[alt="Реклама"] {
    width: 100%;
    height: auto;
  }

  .p-2 {
    padding: 8px !important;
  }

  .col-12.px-5 {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
